"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Textarea } from "@/components/ui/textarea";
import { Loader2, MessageSquare, Send, Bot, User, Zap } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { AI_MODELS } from "@/lib/ai";

interface ChatMessage {
  id: string;
  role: 'user' | 'assistant';
  content: string;
  timestamp: Date;
  model?: string;
  score?: number;
  responseTime?: number;
  orchestratorUsed?: boolean;
}

interface AIResponse {
  model: string;
  response: string;
  score: number;
  responseTime: number;
  error?: string;
}

export default function AIChatPage() {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [inputMessage, setInputMessage] = useState("");
  const [selectedModels, setSelectedModels] = useState<string[]>(["gpt-4o-mini"]);
  const [isLoading, setIsLoading] = useState(false);
  const [orchestratorMode, setOrchestratorMode] = useState(true);
  const [lastResponses, setLastResponses] = useState<AIResponse[]>([]);

  const availableModels = [
    { id: "gpt-4o-mini", name: "GPT-4o Mini", provider: "OpenRouter" },
    { id: "llama3.1", name: "Llama 3.1", provider: "Ollama" },
    { id: "deepseek-coder-v2-236b", name: "DeepSeek Coder V2", provider: "DeepSeek" },
  ];

  const handleSendMessage = async () => {
    if (!inputMessage.trim()) return;

    const userMessage: ChatMessage = {
      id: Date.now().toString(),
      role: 'user',
      content: inputMessage,
      timestamp: new Date(),
    };

    setMessages(prev => [...prev, userMessage]);
    const currentMessage = inputMessage;
    setInputMessage("");
    setIsLoading(true);

    try {
      const response = await fetch("/api/ai", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          prompt: currentMessage,
          models: selectedModels,
          orchestrator: orchestratorMode,
        }),
      });

      if (!response.ok) {
        throw new Error(`API error: ${response.status}`);
      }

      const data = await response.json();

      if (!data.success) {
        throw new Error(data.error || 'AI request failed');
      }

      // Store all responses for analysis
      setLastResponses(data.allResponses);

      const assistantMessage: ChatMessage = {
        id: (Date.now() + 1).toString(),
        role: 'assistant',
        content: data.bestResponse.response,
        timestamp: new Date(),
        model: data.orchestratorUsed 
          ? `${data.bestResponse.model} (Best of ${data.allResponses.length})`
          : data.bestResponse.model,
        score: data.bestResponse.score,
        responseTime: data.bestResponse.responseTime,
        orchestratorUsed: data.orchestratorUsed,
      };

      setMessages(prev => [...prev, assistantMessage]);

    } catch (error) {
      console.error("Error sending message:", error);
      const errorMessage: ChatMessage = {
        id: (Date.now() + 1).toString(),
        role: 'assistant',
        content: `Sorry, I encountered an error: ${error instanceof Error ? error.message : 'Unknown error'}. Please try again.`,
        timestamp: new Date(),
        model: "Error",
      };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  const toggleModel = (modelId: string) => {
    setSelectedModels(prev => 
      prev.includes(modelId) 
        ? prev.filter(id => id !== modelId)
        : [...prev, modelId]
    );
  };

  const formatResponseTime = (ms: number) => {
    return ms > 1000 ? `${(ms / 1000).toFixed(1)}s` : `${ms}ms`;
  };

  return (
    <div className="flex-1 space-y-6 p-4 md:p-6">
      <div className="flex items-center gap-2">
        <MessageSquare className="w-6 h-6 text-[#FF5C5C]" />
        <h1 className="text-3xl font-bold tracking-tight">AI Chat with Orchestrator</h1>
      </div>
      <p className="text-gray-600">Chat with multiple AI models and use the orchestrator to get the best responses</p>

      <div className="grid gap-6 lg:grid-cols-4">
        {/* Chat Interface */}
        <div className="lg:col-span-3">
          <Card className="h-[600px] flex flex-col">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Zap className="w-5 h-5" />
                AI Chat Interface
              </CardTitle>
              <CardDescription>
                {orchestratorMode && selectedModels.length > 1 
                  ? `ðŸŽ¯ Orchestrator mode: Using ${selectedModels.length} models`
                  : `ðŸ¤– Single model: ${availableModels.find(m => m.id === selectedModels[0])?.name || 'None selected'}`
                }
              </CardDescription>
            </CardHeader>
            <CardContent className="flex-1 flex flex-col">
              {/* Messages */}
              <div className="flex-1 overflow-y-auto space-y-4 mb-4">
                {messages.length === 0 ? (
                  <div className="flex items-center justify-center h-full text-gray-500">
                    <div className="text-center">
                      <Bot className="w-12 h-12 mx-auto mb-2 opacity-50" />
                      <p>Start a conversation with AI</p>
                      <p className="text-xs mt-1">Select models and enable orchestrator for best results</p>
                    </div>
                  </div>
                ) : (
                  messages.map((message) => (
                    <div
                      key={message.id}
                      className={`flex gap-3 ${
                        message.role === 'user' ? 'justify-end' : 'justify-start'
                      }`}
                    >
                      <div
                        className={`max-w-[80%] rounded-lg p-3 ${
                          message.role === 'user'
                            ? 'bg-[#FF5C5C] text-white'
                            : 'bg-gray-100 text-gray-900'
                        }`}
                      >
                        <div className="flex items-center gap-2 mb-1">
                          {message.role === 'user' ? (
                            <User className="w-4 h-4" />
                          ) : (
                            <Bot className="w-4 h-4" />
                          )}
                          <span className="text-xs opacity-75">
                            {message.role === 'user' ? 'You' : message.model}
                          </span>
                          {message.score && (
                            <Badge variant="outline" className="text-xs">
                              Score: {message.score.toFixed(1)}
                            </Badge>
                          )}
                          {message.responseTime && (
                            <Badge variant="outline" className="text-xs">
                              {formatResponseTime(message.responseTime)}
                            </Badge>
                          )}
                        </div>
                        <p className="text-sm">{message.content}</p>
                      </div>
                    </div>
                  ))
                )}
                {isLoading && (
                  <div className="flex justify-start">
                    <div className="bg-gray-100 rounded-lg p-3">
                      <div className="flex items-center gap-2">
                        <Loader2 className="w-4 h-4 animate-spin" />
                        <span className="text-sm text-gray-600">
                          {orchestratorMode && selectedModels.length > 1 
                            ? `Querying ${selectedModels.length} models...`
                            : 'AI is thinking...'
                          }
                        </span>
                      </div>
                    </div>
                  </div>
                )}
              </div>

              {/* Input */}
              <div className="flex gap-2">
                <Textarea
                  placeholder="Ask me anything... (Press Enter to send, Shift+Enter for new line)"
                  value={inputMessage}
                  onChange={(e) => setInputMessage(e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' && !e.shiftKey) {
                      e.preventDefault();
                      handleSendMessage();
                    }
                  }}
                  className="flex-1 min-h-[60px]"
                />
                <Button
                  onClick={handleSendMessage}
                  disabled={isLoading || !inputMessage.trim() || selectedModels.length === 0}
                  className="bg-[#FF5C5C] hover:bg-[#E54545]"
                >
                  <Send className="w-4 h-4" />
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Settings Panel */}
        <div className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>AI Models</CardTitle>
              <CardDescription>Select models to query</CardDescription>
            </CardHeader>
            <CardContent className="space-y-3">
              {availableModels.map((model) => (
                <div key={model.id} className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    id={model.id}
                    checked={selectedModels.includes(model.id)}
                    onChange={() => toggleModel(model.id)}
                    className="rounded"
                  />
                  <label htmlFor={model.id} className="text-sm flex-1">
                    <div className="font-medium">{model.name}</div>
                    <div className="text-xs text-gray-500">{model.provider}</div>
                  </label>
                </div>
              ))}
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Orchestrator</CardTitle>
              <CardDescription>AI response optimization</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="flex items-center space-x-2">
                <input
                  type="checkbox"
                  id="orchestrator"
                  checked={orchestratorMode}
                  onChange={(e) => setOrchestratorMode(e.target.checked)}
                  className="rounded"
                />
                <label htmlFor="orchestrator" className="text-sm">
                  Enable orchestrator mode
                </label>
              </div>
              <p className="text-xs text-gray-500 mt-2">
                When enabled with multiple models, all models are queried in parallel and the best response is selected based on quality scoring.
              </p>
            </CardContent>
          </Card>

          {/* Last Response Analysis */}
          {lastResponses.length > 0 && (
            <Card>
              <CardHeader>
                <CardTitle>Response Analysis</CardTitle>
                <CardDescription>Last query results</CardDescription>
              </CardHeader>
              <CardContent className="space-y-2">
                {lastResponses.map((response, index) => (
                  <div key={index} className="text-xs p-2 bg-gray-50 rounded">
                    <div className="font-medium">{response.model}</div>
                    <div className="flex justify-between text-gray-600">
                      <span>Score: {response.score.toFixed(1)}</span>
                      <span>{formatResponseTime(response.responseTime)}</span>
                    </div>
                    {response.error && (
                      <div className="text-red-600 mt-1">Error: {response.error}</div>
                    )}
                  </div>
                ))}
              </CardContent>
            </Card>
          )}

          <Card>
            <CardHeader>
              <CardTitle>Status</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <div className="flex justify-between text-sm">
                  <span>Selected Models:</span>
                  <Badge variant="outline">{selectedModels.length}</Badge>
                </div>
                <div className="flex justify-between text-sm">
                  <span>Messages:</span>
                  <Badge variant="outline">{messages.length}</Badge>
                </div>
                <div className="flex justify-between text-sm">
                  <span>Mode:</span>
                  <Badge variant={orchestratorMode ? "default" : "secondary"}>
                    {orchestratorMode ? "ðŸŽ¯ Orchestrator" : "ðŸ¤– Single"}
                  </Badge>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
